body {
    margin: 0;
    font-family: Arial, sans-serif;
  }
  .home-container {
    text-align: center;
    padding: 20px;
  }
  .navbar {
    display: flex;
    justify-content: flex-end;
    background-color: #fff;
    padding: 20px;
  }
  .navbar ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  .navbar ul li {
    display: inline;
    margin: 0 15px;
    font-size: 1.2em;
    cursor: pointer;
  }
  .contentin{
    display: flex;
    gap: 20px;
    justify-content: space-around;
    align-items: center;
    margin: 40px ;
  }
  .main-content {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 40px 0;
  }
  .text-section {
    max-width: 400px;
    text-align: left;
  }
  .text-section h1 {
    font-size: 3em;
  }
  .text-section p {
    font-size: 1.2em;
    color: #555;
  }
  .card {
    background-color: #333;
    color: white;
    padding: 40px;
    border-radius: 10px;
    width: 300px;
    position: relative;
  }
  .card h2 {
    font-size: 2em;
  }
  .card p {
    margin: 20px 0;
  }
  .card button {
    background-color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 20px;
    font-size: 1em;
    cursor: pointer;
  }
  .toggle-switch {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
  }
  .toggle-switch span {
    margin-right: 10px;
    font-size: 1.2em;
  }
  .switch {
    width: 60px;
    height: 30px;
    background-color: #ddd;
    border-radius: 15px;
    position: relative;
  }
  .toggle {
    width: 30px;
    height: 30px;
    background-color: white;
    border-radius: 50%;
    position: absolute;
    left: 0;
    top: 0;
    transition: 0.3s;
  }
  .profile-container {
    display: flex;
    flex-wrap: wrap; 
    align-items: center;
    justify-content: space-around;
    padding: 20px;
    gap: 30px;
  }
  .profile-card {
    width: 300px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 10px;
    text-align: center;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition: box-shadow 0.3s ease;
  }
  .profile-img {
    border-radius: 50%;
    width: 150px;
    height: 150px;
    margin-bottom: 20px;
  }
  .read-more {
    background: none;
    border: none;
    color: rgb(191, 191, 199);
    cursor: pointer;
    text-decoration: underline;
  }
  .read-more:hover {
    color: darkblue;
  }

@media (max-width: 768px) {
  .navbar {
    flex-wrap: wrap; 
    justify-content: center;
  }
  
  .contentin {
    flex-direction: column; 
    gap: 20px;
    margin: 20px;
  }

  .main-content {
    flex-direction: column; 
    align-items: center;
    margin: 20px 0;
  }

  .text-section {
    max-width: 90%; 
    text-align: center;
  }

  .card {
    width: 90%;
    padding: 20px;
  }

  .profile-container {
    flex-direction: column; 
    gap: 20px;
    padding: 10px;
  }

  .profile-card {
    width: 90%;
    padding: 15px;
  }
}

@media (max-width: 480px) {
  .navbar ul li {
    font-size: 0.9em; 
    margin: 0 5px;
  }

  .text-section h1 {
    font-size: 2em; 
  }

  .text-section p {
    font-size: 1em;
  }

  .card button {
    width: 100%;
  }

  .profile-img {
    width: 120px;
    height: 120px;
  }

  .toggle-switch {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
}
